header {
  background-color: $color1;
  color: #fff;
  padding: 2rem 0;
  & .contact-btn {
    display: none;
  }
  .navbar {
    display: flex;
    justify-content: space-between;
    // width: 115px;
    & svg {
      align-self: center;
    }
    .nav-links {
      padding-inline-start: 80px;
      display: none;
      align-items: center;
      justify-content: space-between;
      width: 115px;
      & a {
        color: inherit;
        text-decoration: none;
      }
    }
  }

  @media screen and (min-width: '750px') {
    & .navbar {
      min-width: 387px;

      & svg {
        display: none;
      }
      .nav-links {
        display: flex;
      }
    }
    & .contact-btn {
      display: block;
    }
  }
}

.contact-btn {
  cursor: pointer;
  border-radius: 25px;
  width: 153px;
  font-size: 1.125rem;
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
  height: 48px;
}
