.teaser {
  background-color: $color6;
  color: #fff;
  & .main-layout {
    padding: 8rem 0;
    gap: 4rem;

    & h1 {
      font-size: 3rem;
      max-width: 286px;
    }
    .teaser-cards {
      text-align: center;

      display: flex;
      flex-direction: column;
      gap: 3rem;
      .teaser-card {
        display: flex;
        gap: 1rem;
        flex-direction: column;
        align-items: center;
        & img {
          width: 65px;
          height: 65px;
        }
        & .card-text {
          display: flex;
          flex-direction: column;
          gap: 1rem;
          & h4 {
            font-size: 1.2rem;
            color: $color2;
          }
          & p {
            max-width: 500px;
            line-height: 1.5;
            font-size: 1.1rem;
          }
        }
      }
    }
  }
  @media screen and (min-width: '750px') {
    & .main-layout {
      gap: unset;
      & .teaser-cards {
        & .teaser-card {
          align-items: flex-start;
          flex-direction: row;
          & .card-text {
            & h1 {
              max-width: 417px;
            }
            & p {
              font-size: 1.05rem;
            }
          }
        }
      }
    }
  }
}
