.cards-container {
  background-color: $color5;
  color: white;
  & .main-layout {
    flex-direction: column;
    align-items: center;
    gap: 84px;
    text-align: center;
    padding: 8rem 0;
    & h1 {
      font-size: 3rem;
      font-size: 2.285714285714286rem;
      font-weight: 700;
      font-family: LivvicMedium;
    }
    & .cards {
      gap: 6rem;
      position: relative;
      display: flex;
      flex-direction: column;
      & .card {
        display: flex;
        flex-direction: column;
        position: relative;
        align-items: center;
        justify-content: center;
        & .card-text {
          z-index: 10;
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 30px;
          & p {
            max-width: 340px;
            line-height: 25px;
            font-size: 1.1rem;
          }
          & h5 {
            color: $color3;
            font-size: 1.285714285714286rem;
          }
        }
        & img:first-child {
          position: absolute;
          top: -36px;

          z-index: 5;
        }
        & img:last-child {
          margin-top: 52px;
          width: 62px;
          border: 1px solid #c4fffe;
          border-radius: 30px;
        }
      }
    }
  }
  @media screen and (min-width: '750px') {
    & .main-layout {
      gap: 56px;

      & h1 {
        font-size: 3rem;
      }
      & .cards {
        flex-direction: row;
        gap: 30px;

        & .card {
          & .card-text {
            & p {
              font-size: 1rem;
            }
            & h5 {
              font-size: 1.125rem;
            }
          }
          & img:first-child {
          }
          & img:last-child {
          }
        }
      }
    }
  }
}
