footer {
  background-color: $color7;
  color: #fff;
  & .main-layout {
    padding: 3rem 0;
    gap: 40px;
    & .footer-nav {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 24px;
      & .footer-nav-txt {
        color: #afafaf;
      }
      & .navbar {
        display: flex;
        flex-direction: column;
        gap: 25px;
        & .nav-links {
          display: flex;
          justify-content: space-between;
          .nav-link {
            color: #fff;
            text-decoration: none;
          }
        }
      }
    }
    & .footer-right {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      gap: 16px;
      & .footer-icons {
        display: flex;
        gap: 1rem;
      }
      & p {
        color: rgb(175, 175, 175);
      }
    }
  }
  & svg {
    display: none;
  }
  @media screen and (min-width: '750px') {
    & .main-layout {
      gap: unset;
      & .footer-nav {
        flex-direction: row;
        align-items: unset;
        gap: 7.8125rem;
      }
      & .footer-right {
        align-items: flex-end;
        gap: unset;
      }
    }
  }
}
