.main-layout {
  margin: 0 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @media screen and (min-width: '750px') {
    flex-direction: row;
    margin: 0 10rem;
  }
}

.logo {
  width: 127px;
  @media screen and (min-width: '750px') {
    width: unset;
  }
}
