.get-started {
  background-color: $color2;
  color: #000;
  & .main-layout {
    padding: 4.75rem 0;
    align-items: center;
    text-align: center;
    gap: 24px;
    & h1 {
      padding: 0 6.125rem;
      font-size: 3rem;
      font-family: LivvicMedium;
    }
    & .contact-btn {
      color: #000;
      border: 0;
      outline: 2px solid #000;
      font-weight: 700;
    }
  }
  @media screen and (min-width: '750px') {
    & .main-layout {
      text-align: unset;
      gap: unset;
      & h1 {
      }
      & .contact-btn {
      }
    }
  }
}
