.hero {
  background-color: $color1;
  color: white;
  & .main-layout {
    align-items: center;
    padding: 3.714rem 0;

    text-align: center;
    gap: 1.285rem;
    & h1 {
      font-size: 2.857rem;
      max-width: 225px;

      & span {
        color: $color2;
      }
    }
    & p {
      font-size: 1.1rem;
      max-width: 315px;
      line-height: 28px;
    }
  }
  @media screen and (min-width: '750px') {
    & .main-layout {
      align-items: flex-end;
      text-align: unset;
      gap: unset;
      padding: 11rem 0;
      & h1 {
        font-size: 6.5rem;
        max-width: 550px;
      }
      & p {
        width: 30%;
        line-height: 1.4;
      }
    }
  }
}
